import React from 'react';
import 'bulma/css/bulma.css';
import './ContactFormAndFooter.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons';

const Footer = () => {
  return (
    <footer className='footer is-paddingless' id="footer">
      <div className='hero has-text-centered has-text-success margined'> 
        <h1 className='title has-text-success'>  
          Get To Know Me 
        </h1>  
      </div>
      <div className='content has-text-centered'>
        <div className='columns is-centered is-mobile'>
          <a className='column is-narrow' href='https://github.com/kyrolos?tab=repositories'>
            <FontAwesomeIcon icon={faGithub} className='fa-2x has-text-success' />
          </a>
          <a className='column is-narrow' href='https://www.linkedin.com/in/kyrolos-magdy-575055163/'>
            <FontAwesomeIcon icon={faLinkedin} className='fa-2x has-text-success' />
          </a>
        </div>
        </div>
    </footer>
  )
}

export default Footer;