import React from 'react';
import 'bulma/css/bulma.css';
import './Projects.css';
import Fade from 'react-reveal/Fade';
// imported components
import IndividualProjects from './IndividualProjects';
// images
import MyPortofolio from './images/MyProtofolio.PNG';
import RopoFriend from './images/RopoFriend.PNG';
import SmartBrain from './images/SmartBrain.PNG';

const Projects = () => {
  return (
    <section 
      className='section content has-text-centered is-marginless project-section'
      id='projects'  
    >
      <Fade left>
        <h1 className='title is-1 has-text-white'>Projects</h1>
      </Fade>
      <Fade right>
        <h4 className='subtitle is-4 has-text-white has-text-weight-light'>Currently, I am looking for a new code adventure</h4>
      </Fade>
      <Fade left>
        <h4 className='subtitle is-4 has-text-white has-text-weight-light'>Here are some recent projects I have completed.</h4>
      </Fade>
      <Fade right>
        <div className='columns is-mobile is-multiline is-variable is-3 is-centered projects-container'>
          <IndividualProjects
            title = 'RopoFriendsApp'
            link = 'https://kyrolos.github.io/Robofriend-app/'
            backgroundImage = {RopoFriend}
            description = "as a problem solver this app with quite entertainment it's my RopoFriendsApp try it you woll find it quite entertaining"
            techStack = {['React', 'Redux', 'Node.js', 'Express.js' , 'Tachyons (css framework)']}
          />
          <IndividualProjects
            title = 'MyPortofolio'
            link = '/'
            backgroundImage = {MyPortofolio}
            description = 'Similar to Jeopardy app. Set up for three teams to go head to head in full rounds of questions with a final Jeopardy round. Great for family and friends.'
            techStack = {['React', 'Bulma(css framework)']}
          />
          <IndividualProjects
            title = 'Smart-brain'
            link = 'https://github.com/kyrolos/faceRecognition'
            backgroundImage = {SmartBrain}
            description = "The Smart-brain App is gonna daetect you face give it a try"
            techStack = {['React', 'Redux', 'express', 'postgresql', 'Heroku', 'node.js', 'jest(still under deploying)']}
          />
        </div>
      </Fade>
    </section>
  )
}

export default Projects;