import React from 'react';
import 'bulma/css/bulma.css';
import './Skills.css';

const SkillBarGraph = () => {
  return (
    <div className='container skills column is-full-mobile'>
        <ul className='lines'>
          <li className='line l--0'>
            <span className='line__label has-text-white'>
              What??
            </span>
          </li>
          <li className='line l--25'>
            <span className='line__label has-text-white'>
              The Basics
            </span>
          </li>
          <li className='line l--50'>
            <span className='line__label has-text-white'>
              Intermediate
            </span>
          </li>
          <li className='line l--75'>
            <span className='line__label has-text-white'>
              Advanced
            </span>
          </li>
          <li className='line l--100'>
            <span className='line__label has-text-white'>
              Expert
            </span>
          </li>
        </ul>
      <div className='charts'>
        <div className='chart chart--dev'>
          <span className='chart__title'>Development</span>
          <ul className='chart--horiz'>
            <li className='chart__bar list-item' style={{'width':'70%'}}>
              <span className='chart__label item'>HTML5</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'60%'}}>
              <span className='chart__label item'>CSS3</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'70%'}}>
              <span className='chart__label item'>Javascript</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'65%'}}>
              <span className='chart__label item'>React / Redux</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'52%'}}>
              <span className='chart__label item'>Next.js</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'60%'}}>
              <span className='chart__label item'>Node.js / NPM </span>
            </li>
            <li className='chart__bar list-item' style={{'width':'55%'}}>
              <span className='chart__label item'>Express</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'53%'}}>
              <span className='chart__label item'>POSTGRESQL</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'35%'}}>
              <span className='chart__label item'>Docker</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'85%'}}>
              <span className='chart__label item'>Git</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'52%'}}>
              <span className='chart__label item'>Redis</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'45%'}}>
              <span className='chart__label item'>Jest</span>
            </li>
            <li className='chart__bar list-item' style={{'width':'50%'}}>
              <span className='chart__label item'>TypeScript</span>
            </li> 

          </ul>
        </div>
      </div>
    </div>
  )
}

export default SkillBarGraph;